.thumbnailDiv{
    background-color: #f4a261;
    color: white;
    min-width: 200px;
    height: 200px;
    margin: 5px;
    position: relative;
}
.projectDiv
{
    display: flex;
    justify-content: center;
    align-self: center;
    flex-direction: column;
}
.projectDivRow{
    display: flex;
    overflow: auto;
}
.thumbnailLogo{
    position: relative;
    height: 180px;
    width: 180px;
    top: 10px;
}
.thumbnailTitle{
    position: absolute;
    left:17.5%;
    top: 40%;
    font-size: 20px;
    user-select: none;
 }
 .thumbnailTitleScreen{
    position: absolute;
    top: 12.5%;
    background-color: #eb83f8; 
    height: 75%;
    width: 75%;
    left: 12.5%;
 }
 .thumbnailAnimation
 {
    position: absolute;
    min-width:200px;
    min-height: 200px;
    bottom:0px;
    opacity: 0;
 }
.thumbnailAnimation:hover{
    opacity: 1;
    transition: opacity 0.5s ease-in;
 }
.aboutDiv{
    display:flex;
    width: 75%;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    padding: 5%;
    margin-left: 5%;
}
.aboutHeaderDiv{
    color: white;
    position: relative;
    left : 100px;
    flex-basis: inherit;
    font-size:22px;
    margin:10px 0 10px;
    text-align: left;
}
.aboutHeaderDiv:after{
    content:"";
    height: 3px;
    width: 90px;
    display: block;
    background-color: #eb83f8;
    position: absolute;
    left:-100px;
    top: 15px;
}
.aboutHeaderDiv::before{
    content:"";
    height: 3px;
    width: 470px;
    display: block;
    background-color: #eb83f8;
    position: absolute;
    left: 125px;
    top: 15px;
}
.introH3{
    color: white;
    font-size: 60px;
    margin: 10px 0 40px;
    line-height: 60px;
    font-weight: 800;
    text-align: left;
    width: 800px
}
.seperator{
    content:"";
    height: 3px;
    width: 90px;
    display: block;
    background-color: #eb83f8;
    border-top: 15px;
}
.aboutP{
    color:white;
    font-size: 14px;
    margin-top: 20px;
    font-weight: 300;
    text-align: left;
    width: 700px
}
.languageDiv{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 5%;
    min-width: 80%;
    padding-left: 25%;
}
.languageH4{
    color: white;
    font-size: 22px;
    position: relative;
    left: 110px;
    flex-basis: inherit;
    width: 100px
}
.languageH4::after{
    content: "";
    height: 3px;
    width: 90px;
    display: block;
    background-color: #eb83f8;
    position: absolute;
    left:-100px;
    top: 15px;
}
.skillDiv{
    max-width: 80%;
}
.skills{
    margin-bottom: 25px;
}
.skillName{
    font-size: 17px;
    color: white;
    text-align: left;
}
.skillValue{
    float: right;
    font-size: 17px;
}
.skillBar{
    height: 10px;
    margin-top: 10px;
    animation: expand 1s;
    background-color: white;

    margin-bottom: 20px;
    overflow: hidden;
    border-radius: 4px;
    box-shadow: inset 0 1px 2px rgba(0,0,0,.1);
}
.projectWrapper{
    position: absolute;
    display: flex;
    flex-direction: column;
    min-height: 100%;
    min-width: 100%;
}
.projectTitle{
    display: flex;
    justify-content: space-between;
    flex-grow: 4;
    align-items: center;
    padding: 0px 50px;
    background-color: #2A9D8F;
    position: relative;
}
.projectMain{
    display: flex;
    justify-content: center;
    flex-grow: 52;
    align-items: center;
    padding: 0px 7.5%;
    position: relative;
}
.projectBottom{
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    align-items: center;
    padding: 0px 50px;
    background-color: #2A9D8F;
    position: relative;
}
.projectPicDiv{
    position: absolute;
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: darkgray;
}
.projectPic{
    max-height: 100%;
    max-width: 100%;
    display: block;
}
.projectName{
    position: absolute;
    color:white;
    font-weight: 650;
    left: 200px;

}
.projectName::after{
    content: "";
    height: 3px;
    width: 90px;
    background-color: #eb83f8;
    position: absolute;
    left: -140px;
    top: 50%
}
.projectX{
    position: absolute;
    right: 0px;
    width: 7.5%;
    height: 100%;
}
.projectXButton{
    background-color: #264653;
    border: none;
    color:#2A9D8F;
    width: 100%;
    height: 100%;
    padding:0px;
}
.projectXButton:hover{
    background-color: #eb83f8;
    color: white;
}
.arrowButtonLeft{
    position: absolute;
    height: 100%;
    width: 10%;
    left: 0px;
    background-color: darkgray;
}
.arrowButtonRight{
    position: absolute;
    height: 100%;
    width: 10%;
    right: 0px;
    background-color: darkgray;
}
.arrowButton{
    position: relative;
    top: 42.5%;
    border-radius: 50%;
    background-color: #ee81ee;
    height: 15%;
    width: 70%;
    border: none;
    outline: none;
}
.arrowButton:hover{
    background-color: turquoise;
    color: white;
}
.arrowButton:disabled{
    background-color: #BE67F1;
    color:black
}
.projectLogo{
    top: 3px;
   width : 3%;
   padding: 0px;
   position: relative;
   left: 115px
}

@keyframes expand{
    from {width: 0%;}
}