@media (min-width:600px) {
  div {
    font-size: 1vh;
  }
}

@media (min-width:800px) {
  div {
    font-size: 2vh;
  }
}

@media (min-width:1000px) {
  div {
    font-size: 3vh;
  }
}

.App {
  text-align: center;
  background-color: black;
  font: 'Poppins';
}
.wot{
  min-width: 100%;
  display: flex;
  justify-content: center;
}
.Wrapper{
  background-color: black;
  padding: 0px 50px 0px 50px;
  align-items: center;
  height: 100vh;
  
  display:flex;
  flex-wrap:inherit;
  overflow: hidden;
  max-height: 100%;
}
.buttonDiv{
  display: flex;
  position: absolute;
  top: 4%;
}
.buttonDiv > button{
  background-color: transparent;
  border: none;
  outline: none;
}
.buttonDiv > button > span{
  margin-right: 40px;
  position: relative;
  cursor: pointer;
  padding-bottom: 2px;
  font-weight: bolder;
  color: white;
}
.buttonDiv > button > span:after{
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  left: 0;
  bottom: 0;
  visibility: hidden;
  background-color: #eb83f8;
  transition: all .3s ease;
  transform: scaleX(0);
}
.buttonDiv > button >span:hover:after,
.buttonDiv > button >span.active:after{
  visibility: visible;
  transform: scaleX(1);
}
.divLeft{
  background-color: #2A9D8F;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 5%;
  flex:25%;
  height: 65vh;
}
.divRight{
  background-color: #264653;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px;
  overflow: hidden; 
  flex:75%;  
  height: 65vh;
  padding:5%;
  flex-direction: column;
  position: relative;
}
.imageHome{
  height: 65vh;
}
.divLeftContent{
  z-index:50;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.glitch{
  position: relative;
  color: white;
  font-weight: 900;
  animation: glitch-skew 1s linear infinite alternate-reverse;
}
.glitch:after,.glitch:before{
  content:attr(data-text);
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.glitch:before{
  left:2px;
  text-shadow: -2px 0 #ff00c1;
  clip: rect(44px,450px,56px,0);
  animation: glitch-anim 5s linear infinite alternate-reverse;
}
.glitch:after{
  left:-2px;
  text-shadow: -2px 0 #00fff9,
  2px 2px #ff00c1;
  animation: glitch-anim2 1s linear infinite alternate-reverse;
}

.Typewriter__wrapper{
  color:white;
  animation: Typewriter-wrapper 4s steps(14,end);
}
.Typewriter__cursor{
  color:#eb83f8;
  animation: Typewriter-cursor 1s infinite;
  margin-left: 1px;
}
.hover-button{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 180px;
  height: 50px;
  background: black;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  line-height: 50px;
  color: white;
  font-size: 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  letter-spacing: 4px;
  border: none;
  margin-top: 20px;
}
.projectDisplayDiv{
  position: absolute;
  top: 0px;
  left: 0px;
  min-width: 100%;
  min-height: 100%;
  background-color: gray;
  opacity: 50%;
}
.projectDiv2
{
  position: absolute;
  left: 5%;
  top: 10%;
  min-width: 90%;
  min-height: 80%;
  background-color: gray;
}


@keyframes Typewriter-wrapper{
  from {width: 0px;}
  to{width:500px;}
}
@keyframes Typewriter-cursor{
  0%{opacity: 0;}
  50%{opacity: 1;}
  100%{opacity: 0;}
}
@keyframes glitch-anim{
  0%{clip:rect(85px,9999px,45px,0);transform:skew(.54deg)}
  5%{clip:rect(68px,9999px,61px,0);transform:skew(.12deg)}
  10%{clip:rect(78px,9999px,54px,0);transform:skew(.64deg)}
  15%{clip:rect(13px,9999px,72px,0);transform:skew(.84deg)}
  20%{clip:rect(91px,9999px,24px,0);transform:skew(.54deg)}
  25%{clip:rect(42px,9999px,28px,0);transform:skew(.97deg)}
  30%{clip:rect(97px,9999px,13px,0);transform:skew(1deg)}
  35%{clip:rect(92px,9999px,64px,0);transform:skew(.26deg)}
  40%{clip:rect(88px,9999px,48px,0);transform:skew(.86deg)}
  45%{clip:rect(90px,9999px,24px,0);transform:skew(.82deg)}
  50%{clip:rect(81px,9999px,5px,0);transform:skew(.96deg)}
  55%{clip:rect(45px,9999px,78px,0);transform:skew(.05deg)}
  60%{clip:rect(90px,9999px,99px,0);transform:skew(.2deg)}
  65%{clip:rect(16px,9999px,66px,0);transform:skew(.39deg)}
  70%{clip:rect(72px,9999px,82px,0);transform:skew(.56deg)}
  75%{clip:rect(8px,9999px,8px,0);transform:skew(.81deg)}
  80%{clip:rect(37px,9999px,62px,0);transform:skew(.24deg)}
  85%{clip:rect(62px,9999px,59px,0);transform:skew(.47deg)}
  90%{clip:rect(77px,9999px,14px,0);transform:skew(.39deg)}
  95%{clip:rect(99px,9999px,91px,0);transform:skew(.9deg)}
  to{clip:rect(78px,9999px,30px,0);transform:skew(.51deg)}}

@keyframes glitch-anim2{
  0%{clip:rect(55px,9999px,29px,0);transform:skew(.59deg)}
  5%{clip:rect(55px,9999px,30px,0);transform:skew(.49deg)}
  10%{clip:rect(69px,9999px,5px,0);transform:skew(.06deg)}
  15%{clip:rect(16px,9999px,58px,0);transform:skew(.41deg)}
  20%{clip:rect(42px,9999px,8px,0);transform:skew(.57deg)}
  25%{clip:rect(46px,9999px,36px,0);transform:skew(.71deg)}
  30%{clip:rect(39px,9999px,33px,0);transform:skew(.09deg)}
  35%{clip:rect(69px,9999px,81px,0);transform:skew(.3deg)}
  40%{clip:rect(73px,9999px,20px,0);transform:skew(.71deg)}
  45%{clip:rect(94px,9999px,90px,0);transform:skew(.32deg)}
  50%{clip:rect(54px,9999px,93px,0);transform:skew(.16deg)}
  55%{clip:rect(60px,9999px,12px,0);transform:skew(.05deg)}
  60%{clip:rect(69px,9999px,69px,0);transform:skew(.03deg)}
  65%{clip:rect(16px,9999px,43px,0);transform:skew(.69deg)}
  70%{clip:rect(69px,9999px,55px,0);transform:skew(.97deg)}
  75%{clip:rect(11px,9999px,39px,0);transform:skew(.05deg)}
  80%{clip:rect(2px,9999px,41px,0);transform:skew(.27deg)}
  85%{clip:rect(62px,9999px,72px,0);transform:skew(.46deg)}
  90%{clip:rect(39px,9999px,94px,0);transform:skew(.45deg)}
  95%{clip:rect(43px,9999px,65px,0);transform:skew(.63deg)}
  to{clip:rect(67px,9999px,57px,0);transform:skew(.33deg)}}

  @keyframes glitch-skew{
    0%{transform:skew(-3deg)}
    10%{transform:skew(-4deg)}
    20%{transform:skew(1deg)}
    30%{transform:skew(4deg)}
    40%{transform:skew(2deg)}
    50%{transform:skew(-3deg)}
    60%{transform:skew(1deg)}
    70%{transform:skew(5deg)}
    80%{transform:skew(0deg)}
    90%{transform:skew(4deg)}
    to{transform:skew(5deg)}}